import React, {useEffect} from 'react'

import { BsFillGridFill, BsList } from 'react-icons/bs'
import styled from 'styled-components'
import {useDispatch, useSelector} from "react-redux";
import {setGridView, setListView, updateSort, Sort_products, filter_Products} from "../store/slice_Filter_Products"

const Sort = () => {

  const {grid_view, filtered_products, sort, filters} = useSelector((store) => store.slice_Filter_Products)
  const {products} = useSelector((store) => store.slice)
  const dispatch = useDispatch();

  const SortUpdate = (e) =>{
    // const name = e.target.name
    const value =  e.target.value
    dispatch(updateSort(value))
  }
  useEffect(() => {
    dispatch(filter_Products())
    dispatch(Sort_products())
  }, [products, sort, filters]);
  
  


  return <Wrapper>
    <div className="btn-container">
      <button type={'button'} aria-label={'active'} className={`${grid_view? 'active': null}`} onClick={()=>dispatch(setGridView())}>
        <BsFillGridFill/>
      </button>
      <button type={'button'} aria-label={'active'} className={`${!grid_view? 'active': null}`} onClick={()=>dispatch(setListView())}>
        <BsList />
      </button>
    </div>
  <p>
    {filtered_products.length} products found
  </p>
    <hr/>
    <form>
      <label htmlFor={'sort'}> sort by </label>
      <select
          name={'sort'}
          id={'sort'}
          className={'sort-input'}
          value={sort}
          onChange={SortUpdate}
      >
        <option value="price-lowest">price(lowest)</option>
        <option value="price-highest">price(highest)</option>
        <option value="name-a">price(a-z)</option>
        <option value="name-z">price(z-a)</option>
      </select>
    </form>
  </Wrapper>
}

const Wrapper = styled.section`
  display: grid;
  grid-template-columns: auto auto 1fr auto;
  align-items: center;
  margin-bottom: 2rem;
  column-gap: 2rem;
  @media (max-width: 576px) {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 0.75rem;
    .btn-container {
      width: 50px;
    }
    label {
      display: inline-block;
      margin-right: 0.5rem;
    }
  }
  @media (min-width: 768px) {
    column-gap: 2rem;
  }
  p {
    text-transform: capitalize;
    margin-bottom: 0;
  }

  .btn-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 0.5rem;
    button {
      background: transparent;
      border: 1px solid var(--clr-black);
      color: var(--clr-black);
      width: 1.5rem;
      border-radius: var(--radius);
      height: 1.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      svg {
        font-size: 1rem;
      }
    }
    .active {
      background: var(--clr-black);
      color: var(--clr-white);
    }
  }

  .sort-input {
    border-color: transparent;
    font-size: 1rem;
    text-transform: capitalize;
    padding: 0.25rem 0.5rem;
  }
  label {
    font-size: 1rem;
    text-transform: capitalize;
  }
`

export default Sort
